:root {
  --body-color: white;
  --heading-color: #252525;
  --btn-color: #fff;
  --btnP-color: rgba(0, 0, 0, 0.29);
  --border-color: #dad7d7;
  --tweetInfo-color: #969696;
  --tweetIcon-color: #697784;
  --textArea-color: white;
}

[data-theme="dark"] {
  --btn-color: #161625;
  --font-color: #e1e1ff;
  --body-color: #161625;
  --border-color: #26263b;
  --btnP-color: rgba(89, 88, 92, 0.301);
  --heading-color: #e4e4e4;
  --tweetInfo-color: #535363;
  --tweetIcon-color: #26263b;
  --textArea-color: #131322;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: var(--body-color);
  color: var(--heading-color);
}

a {
  text-decoration: none;
  color: var(--heading-color);
}

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 10px;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.btn {
  text-transform: uppercase;
  margin: 35px auto;
  padding: 10px;
  color: var(--heading-color);
  border: 1px solid var(--btnP-color);
  cursor: pointer;
  background: var(--body-color);
  font-size: 16px;
  width: 250px;
  position: relative;
}

.btn:hover {
  border-color: rgba(0, 0, 0, 0.5);
  text-decoration: none;
}

.btn:focus {
  outline: 0;
  font-weight: 700;
  border-width: 2px;
}

.center {
  text-align: center;
}

.active {
  font-weight: bold;
}

.nav ul {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  text-decoration: none;
}

.nav li:first-child {
  padding-left: 0;
}

ul {
  padding-left: 0;
}

li {
  list-style-type: none;
  padding: 10px;
  text-decoration: none;
}

.avatar {
  height: 50px;
  margin: 10px;
}

.avatar img {
  border-radius: 25px;
}

.tweet {
  width: 100%;
  max-width: 590px;
  padding: 10px;
  display: flex;
  margin: 0 auto;
  border: 2px solid var(--border-color);
  border-radius: 10px;
  /* cursor: pointer; */
}

.tweet-info {
  margin: 10px;
  display: flex;
  flex-direction: column;
}

/* Hacky but less typing in the video */
.tweet-info > div > span:first-child {
  font-weight: bold;
  margin-right: 5px;
}
.tweet-info > div > span:nth-child(3) {
  color: var(--tweetInfo-color);
  font-size: 16px;
}
.tweet-info > div > div {
  color: var(--tweetInfo-color);
  font-size: 15px;
}
.tweet-info > div > p {
  font-size: 18px;
  margin: 10px 0;
}
.tweet-icons {
  display: flex;
  align-items: center;
}
.tweet-icons > span {
  color: var(--tweetInfo-color);
  margin: 0 15px 0 5px;
  font-size: 18px !important;
}
.tweet-icon {
  font-size: 27px;
  color: var(--tweetIcon-color);
}
.tweet-icon:hover {
  color: #00b5d6;
  cursor: pointer;
}
.replying-to {
  border: none;
  background: transparent;
  margin: 0;
  padding: 0;
  color: var(--tweetInfo-color);
  cursor: pointer;
}

.heart-button {
  background: transparent;
  border: none;
}

.new-tweet {
  width: 100%;
  max-width: 590px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.textarea {
  border-radius: 4px;
  border: 1px solid var(--border-color);
  padding: 10px;
  font-size: 15px;
  height: 100px;
  background-color: var(--textArea-color);
}

.tweet-length {
  text-align: right;
  margin-top: 10px;
  font-size: 20px;
  color: #c3392a;
}

.toggle-btn {
  padding: 0.5rem 1rem;
  margin: 0;
  margin-left: 10px;
  border: 0.1rem solid var(--border-color);
  border-radius: 10rem;
  font-family: sans-serif;
  transition: all 0.2s;
  background-color: var(--body-color);
  text-transform: capitalize;
  color: var(--heading-color);
}
.toggle-btn:hover {
  background-color: var(--border-color);
}

.toggle-btn:focus {
  outline: 0;
}

.pages {
  /* display: flex; */
  /* justify-content: space-between; */
  /* align-items: center; */
  /* max-width: 1000px; */
  margin-left: 35vw;
  /* transition: translateX(50%); */
}

.userid {
  font-size: 14px;
  color: gray;
}

.tweet:hover {
  color: inherit;
}

.origtweet {
  max-height: 20vh;
  /* border: 2px; */
}

.button.ui {
  border-radius: 15px;
  border: 2px solid var(--border-color);
  background: rgb(237, 238, 238);
}

.orig-icon {
  font-size: 17px;
  color: var(--tweetIcon-color);
}

.ui.modal.setting {
  top: 30vh;
  left: 40vw;
}

.ui.modal > .content {
  color: black;
}

.setting-btn {
  padding: 0.5rem 1rem;
  margin: 0;
  border: 0.1rem solid var(--border-color);
  border-radius: 25px;
  font-family: sans-serif;
  transition: all 0.2s;
  background-color: var(--body-color);
  text-transform: capitalize;
  color: var(--heading-color);
}

.setting-btn:hover {
  background-color: var(--border-color);
}

.setting-btn:focus {
  outline: 0;
}

.setting-cont {
  margin-bottom: 15px;
}
